/*------------------------------------------------------------------------------
  Nav Menus
------------------------------------------------------------------------------*/
// General nav styles
nav {
  ul {
    display: flex;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  li {
    position: relative;
    margin: 0;
    padding: 0;
  }

  a {
    display: block;
    width: 100%;
    height: 100%;
  }
}

// Logged Out Menu
.nav--primary {
  a {
    padding: rem(15) rem(10) rem(10);
    font: 400 rem(18)/1 $cursive;
    color: $white;
    text-decoration: none;
  }

.nav-button{
    // height: 10px;
    max-height: 60px;
    width: 220px;
    height: 50px; 
    margin: 0 20px;
    display: flex;
    align-items: center;
    justify-content: center; 
    a {
        @include button($white, transparent, $white, $white, $scooter);
        text-transform: none;
        padding: 0px;
    }
    
    &.ghost a {
        @include button($white, transparent, $white, $white, $teal);
        padding: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:focus,
        &:hover {
            border-color: $white!important;
        }
    }
    &.teal a {
        @include button($teal, $teal, $white, transparent, $teal);
        padding: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:focus,
        &:hover { 
            border-color: $teal;
        }
    }


}

}

// Mega menu left
.nav--left {
  width: calc(100% + 20px);
  margin: rem(40) rem(-10) rem(28);

  ul {
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
  }

  li {
    flex: 0 1 17%;
    width: calc(17% - 20px);
    min-width: rem(130);
    margin: rem(10);
    // background-color: rgba($lightGray, 0.12);
  }

  a {
    padding: rem(15) rem(10) rem(10);
    font: 400 rem(18)/1 $cursive;
    color: $blue;
    text-decoration: none;

    > div {
      position: relative;
      margin-bottom: rem(20);

      img {
        height: rem(60);
        transition: all .4s ease;

        &:last-of-type {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          margin: auto;
          opacity: 0;
          z-index: -1;
        }
      }
    }

    &.active,
    &:hover {
      color: $scooter;

      > div img {
        &:first-of-type {
          opacity: 0;
        }

        &:last-of-type {
          opacity: 1;
        }
      }
    }
  }
}

// Mega menu right
.nav--right {
  padding: rem(10) 0;

  ul {
    flex-wrap: wrap;
  }

  li {
    flex: 0 1 100%;
  }

  a {
    padding: rem(10);
    font: 400 rem(18)/1 $cursive;
    color: $tundora;
    text-decoration: none;

    &:hover {
      color: $teal;
    }
  }
}

// Footer nav
.nav--footer {
  margin-left: rem(50);

  ul {
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  a {
    padding: rem(10);
    font: 400 rem(14)/1.47 $cursive;
    color: $white;
    text-decoration: none;

    &:hover {
      color: $teal;
    }
  }
}

// Woocommerce nav
.woocommerce-MyAccount-navigation {
  margin-bottom: rem(30);

  ul {
    flex-wrap: wrap;
  }

  .woocommerce-MyAccount-navigation-link {
    flex: 0 1 100%;
    text-transform: capitalize;
  }
}

// Media Queries
@media screen and (max-width: $small-breakpoint) {
  .nav--primary .nav-button a {
    padding: rem(12);
    font-size: rem(14);
    border-width: rem(2);
  }

  .nav--left ul {
    justify-content: center;
  }

  .nav--footer {
    flex: 0 1 100%;
    margin: rem(20) 0;

    ul {
      justify-content: center;
    }
  }
}
